import { Button, Box, Grid } from "@mui/material";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";

import { sendData } from "../config/firebasemethods";

function Course() {
  const params = useParams();
  const [txt, setTxt] = useState("");
  const [model, setModel] = useState({});
  const [userId, setUserId] = useState("");
  const navigate = useNavigate();

  const addCourse = () => {
    sendData(model, `courses/`)
      .then((success) => {
        console.log(success);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  let fillModel = (key, val) => {
    model[key] = val;
    setModel({ ...model });

    console.log(model);
  };

  return (
    <>
               <div>
            <h1 style={{
                color: "purple",
                textAlign: "center",
                textShadow: "4px 3px 1px gray"
                
               }}>Course Form</h1></div>

                <div className="form">
                <div className="form-body">

                <label className='label'>Course Name:</label><br /> <input type="text" value={model.firstName} onChange={(e) => fillModel('courseName', e.target.value)}  placeholder="Enter your Course Name..." /><br />

                <label className='label'>Course Duration:</label><br /> <input type="text" value={model.courseDuration} onChange={(e) => fillModel('courseDuration', e.target.value)}  placeholder="Enter your Course Duration..." /><br />

                <label className='label'>Is Form Open:</label><br /> <input type="text" required={true} value={model.isFormOpen} onChange={(e) => fillModel('isFormOpen', e.target.value)}   placeholder="Enter your ......." /><br />

                <label className='label'>No. Of Quiz:</label><br />
                <input type="text"
              required={true}
              onChange={(e) => fillModel('noOfQuiz', e.target.value)}
              datasource={[
                {
                  id: 1,
                  fullName: 1,
                },
                {
                  id: 2,
                  fullName: 2,
                },
                {
                  id: 3,
                  fullName: 3,
                },
                {
                  id: 4,
                  fullName: 4,
                },
                {
                  id: 5,
                  fullName: 5,
                },
              ]}
            /><br />

                <label className='label'>Fee In Rupees:</label><br /> <input type="text" required={true} value={model.feeInRupees} onChange={(e) => fillModel('feeInRupees', e.target.value)} placeholder="Enter your ......." /><br />

                
                <label className='label'>Lead Trainer Id:</label><br /> <input type="text" value={model.leadTrainerId} onChange={(e) => fillModel('leadTrainerId', e.target.value)} placeholder="Enter your ......." /><br />
                <label className='label'>Assistant Trainers:</label><br />
            <input type="text"
              required={true}
              onChange={(e) => fillModel('assistantTrainers', e.target.value)}
              datasource={[
                {
                  id: "b",
                  fullName: ".....",
                },
                {
                  id: "a",
                  fullName: "--------",
                },
                {
                  id: "g",
                  fullName: ",,,,,,,",
                },
              ]}
            /><br />
      
      <div class="footer">
          <input class="btn" onClick={addCourse} type="submit" value="Submit" />
                    </div>

      </div>
      </div>

    </>



 
  );
}
export default Course;
