import { useEffect, useState } from "react";
import { getData } from "../config/firebasemethods";
import { Button, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";

function Courses() {
    const [data, setData] = useState([]);
    useEffect(() => {
        getData("courses")
            .then((res) => {
                console.log(res);
                setData(res);
            })
            .catch((err) => {
                console.log(err);
            });
    }, []);
    return (
        <>

<Box sx={{ textAlign: "center", marginTop: 2 }}> 
<Typography variant="p" sx={{fontWeight : "bold", fontSize : "2.6rem", color: "purple",textAlign: "center",textShadow: "4px 3px 1px gray"}} >Courses</Typography>
</Box>
<div className="form">
           
            <table>
                <tr style={{color: "aqua"}}>
                    <th >Course Name</th>
                    <th>Course Duration</th>
                    <th>Assistant Trainers</th>
                    <th>Lead Trainer Id</th>
                </tr>
                <tbody>
                    {data && data.length > 0 ? data.map((x) => <tr>
                        <td>{x.courseName}</td>
                        <td>{x.courseDuration}</td>
                        <td>{x.assistantTrainers}</td>
                        <td>{x.leadTrainerId}</td>
                    </tr>) : null}
                </tbody>
            </table>

            </div>


            <Box sx={{ textAlign: "center", marginTop: 2 }}> 
<Typography variant="p" sx={{fontWeight : "bold", fontSize : "2.6rem", color: "purple",textAlign: "center",textShadow: "4px 3px 1px gray"}} >Results</Typography>
</Box>
           

<div className="form">
            <table>
                <tr style={{color: "aqua"}}>
                    <th>Course Name</th>
                    
                </tr>
                <tbody>
                    {data && data.length > 0 ? data.map((x) => <tr>
                        <td>{x.courseName}</td>
                        
                    </tr>) : null}
                </tbody>
            </table>

            </div>
        </>
    );
}
export default Courses;